}\r\n\r\n#drawer li a {\r\n  color: white;\r\n  font-size: 20px;\r\n  padding: 10px 1px;\r\n  text-decoration-line: none;\r\n}\r\n\r\n#drawer li a:hover {\r\n  background-color: #fff;\r\n  border: none;\r\n  border-radius: 5px;\r\n  padding: 4px 1px;\r\n  font-weight: bold;\r\n  color: black;\r\n}\r\n#hamburger {\r\n  font-size: 28px;\r\n  width: 44px;\r\n  height: 44px;\r\n  padding-left: 9px;\r\n  text-decoration: none;\r\n  color: rgb(255, 255, 255);\r\n  display: none;\r\n}\r\ndiv#jumbotron {\r\n  margin-top: 0;\r\n  /* width: 100%; */\r\n  height: 420px;\r\n  background-image: url('../public/images/hero-image_2.jpg');\r\n  /* background-position: center; */\r\n  background-repeat: no-repeat;\r\n  background-size: cover;\r\n}\r\nmain {\r\n  padding: 0 10%;\r\n  margin-top: 4%;\r\n}\r\nmain h2 {\r\n  padding: 20px 10vh;\r\n  text-align: center;\r\n  font-weight: bold;\r\n}\r\n/* menggunakna flex / grip () */\r\n/* ketika grid ditetapkan pada wrapper atau container,\r\nseluruh elemen yang berada di dalamnya secara langsung\r\nmerupakan grid item. Namun, kita tidak melihat perubahannya\r\nkarena kita belum mengatur akan seperti apa grid item ditampilkan. */\r\n\r\n*.wrapper {\r\n  display: grid;\r\n  grid-template-columns: repeat(3, 1fr);\r\n  grid-template-rows: repeat(3, 1fr);\r\n  gap: 15px;\r\n}\r\n/* item */\r\n*.wrapper .card {\r\n  box-shadow: 0 8px 10px 0px rgba(63, 62, 62, 0.2);\r\n}\r\ndiv.card.menu-content {\r\n  border: 1px solid rgb(255, 255, 255);\r\n  background-color: #ffde00;\r\n  border-radius: 8px;\r\n  position: relative;\r\n}\r\ndiv.card .card-body {\r\n  margin-top: -15px;\r\n  padding: 0 10px;\r\n  background-color: white;\r\n}\r\ndiv.card img {\r\n  width: 100%;\r\n  height: 250px;\r\n}\r\ndiv.card h5 {\r\n  font-weight: bold;\r\n  font-size: 20px;\r\n  margin: 8px 0;\r\n}\r\ndiv.card p {\r\n  text-align: justify;\r\n  padding-bottom: 4px;\r\n  text-indent: 12px;\r\n  overflow: hidden;\r\n  text-overflow: ellipsis;\r\n  display: -webkit-box;\r\n  -webkit-line-clamp: 3; /* number of lines to show */\r\n  /* line-clamp: 1; */\r\n  -webkit-box-orient: vertical;\r\n  line-height: 1.4em;\r\n}\r\n\r\ndiv.card .ribbon {\r\n  position: absolute;\r\n  background-color: #ffde00;\r\n  padding: 5px 6px;\r\n  font-size: 16px;\r\n  top: 10px;\r\n  left: 4px;\r\n  border-radius: 5px;\r\n}\r\n\r\ndiv.card .rating {\r\n  border-radius: 5px;\r\n  border: none;\r\n  background-color: #000;\r\n  font-size: 15px;\r\n  font-weight: bold;\r\n  padding: 3px;\r\n  color: #fff;\r\n}\r\n#menu-restaurant {\r\n  display: grid;\r\n  grid-template-columns: repeat(2, 1fr);\r\n  grid-template-rows: 1fr;\r\n}\r\n#menu-restaurant p {\r\n  font-size: 18px;\r\n  padding: 6px;\r\n  margin: 0 6px;\r\n  border: none;\r\n  border-radius: 5px;\r\n  box-shadow: 0 8px 10px 0px rgba(63, 62, 62, 0.2);\r\n  display: inline-block;\r\n}\r\n.customer-review {\r\n  box-shadow: 0 8px 10px 0px rgba(63, 62, 62, 0.2);\r\n  padding: 8px;\r\n  width: 520px;\r\n  margin: 8px auto 0;\r\n}\r\n\r\n.like {\r\n  width: 55px;\r\n  height: 55px;\r\n  display: flex;\r\n  align-items: center;\r\n  justify-content: center;\r\n  background-color: #db0000;\r\n  position: fixed;\r\n  bottom: 18px;\r\n  right: 16px;\r\n  border-radius: 50%;\r\n  border: 0;\r\n\r\n  font-size: 18px;\r\n  color: white;\r\n  cursor: pointer;\r\n}\r\n\r\nfooter {\r\n  margin-top: 10vh;\r\n  padding: 18px 0;\r\n  color: white;\r\n  display: flex;\r\n  background-color: #d2001a;\r\n  justify-content: center;\r\n}\r\n/* detail restaurant */\r\n#detail-restaurant {\r\n  box-shadow: 0 8px 10px 0px rgba(63, 62, 62, 0.2);\r\n  margin: 0 auto;\r\n  background-color: chartreuse;\r\n  width: 90vh;\r\n}\r\n\r\n#detail-restaurant .restaurant_poster {\r\n  width: 100%;\r\n  border-radius: 5px;\r\n}\r\n#detail-restaurant .restaurant_info p {\r\n  font-weight: normal;\r\n}\r\n/*Loader*/\r\n#loader {\r\n  position: fixed;\r\n  left: 50%;\r\n  top: 50%;\r\n  z-index: 1;\r\n  margin: -75px 0 0 -75px;\r\n  border: 16px solid rgb(230, 224, 224);\r\n  border-radius: 50%;\r\n  border-top: 16px solid #e70523;\r\n  width: 100px;\r\n  height: 100px;\r\n  -webkit-animation: spin 2s linear infinite;\r\n  animation: spin 2s linear infinite;\r\n}\r\n@-webkit-keyframes spin {\r\n  0% {\r\n    -webkit-transform: rotate(0deg);\r\n  }\r\n  100% {\r\n    -webkit-transform: rotate(360deg);\r\n  }\r\n}\r\n@keyframes spin {\r\n  0% {\r\n    transform: rotate(0deg);\r\n  }\r\n  100% {\r\n    transform: rotate(360deg);\r\n  }\r\n}\r\n/* Add animation to \"page content\" */\r\n#content {\r\n  position: relative;\r\n  -webkit-animation-name: animatebottom;\r\n  -webkit-animation-duration: 1s;\r\n  animation-name: animatebottom;\r\n  animation-duration: 1s;\r\n}\r\n@-webkit-keyframes animatebottom {\r\n  from {\r\n    bottom: -100px;\r\n    opacity: 0;\r\n  }\r\n  to {\r\n    bottom: 0px;\r\n    opacity: 1;\r\n  }\r\n}\r\n@keyframes animatebottom {\r\n  from {\r\n    bottom: -100px;\r\n    opacity: 0;\r\n  }\r\n  to {\r\n    bottom: 0;\r\n    opacity: 1;\r\n  }\r\n}\r\n#review-input {\r\n  width: 520px;\r\n  background-color: rgb(255, 40, 40);\r\n  margin: 10px auto;\r\n  padding: 10px 15px;\r\n  border-radius: 5px;\r\n}\r\n#review-input > h3 {\r\n  text-align: center;\r\n}\r\n.input-review {\r\n  border: none;\r\n  font-family: 'Poppins', sans-serif;\r\n  border-radius: 5px;\r\n  display: block;\r\n  font-size: 20px;\r\n  padding: 10px 4px;\r\n  font-weight: normal;\r\n  margin: 10px 0;\r\n  width: 99%;\r\n  background-color: rgb(238, 237, 237);\r\n  box-shadow: -4px -3px 45px 21px rgba(228, 228, 228, 0.35);\r\n}\r\n.input-review:focus {\r\n  border: none;\r\n}\r\n#send-review {\r\n  background-color: rgb(236, 206, 206);\r\n  padding: 12px;\r\n  font-size: 18px;\r\n  font-weight: bold;\r\n  border-radius: 5px;\r\n  border: none;\r\n  cursor: pointer;\r\n  width: 99%;\r\n}\r\n#send-review:hover {\r\n  background-color: rgb(247, 151, 151);\r\n}\r\n#error-page {\r\n  display: flex;\r\n  justify-content: center;\r\n  margin: 120px 0;\r\n}\r\n\r\n@media (max-width: 950px) {\r\n  main {\r\n    padding: 0 2%;\r\n  }\r\n  div.card img {\r\n    height: 200px;\r\n  }\r\n}\r\n@media screen and (min-width: 600px) and (max-width: 900px) {\r\n  .app-bar {\r\n    padding: 12px 1%;\r\n  }\r\n  main {\r\n    padding: 0 4px;\r\n  }\r\n\r\n  #drawer li a {\r\n    color: white;\r\n    font-size: 16px;\r\n  }\r\n\r\n  *.wrapper {\r\n    display: grid;\r\n    grid-template-columns: repeat(2, 1fr);\r\n    grid-template-rows: repeat(3, 1fr);\r\n    gap: 15px;\r\n  }\r\n\r\n  div.card img {\r\n    /* width: 150px; */\r\n    height: 150px;\r\n  }\r\n  div.card h5 {\r\n    font-size: 16px;\r\n  }\r\n  div.card p {\r\n    font-size: 14px;\r\n  }\r\n}\r\n#error-page {\r\n  display: flex;\r\n  justify-content: center;\r\n  margin: 120px 0;\r\n}\r\n@media screen and (max-width: 600px) {\r\n  .app-bar {\r\n    padding: 10px 6px;\r\n  }\r\n  main {\r\n    padding: 0 10px;\r\n  }\r\n  #drawer {\r\n    padding: 10px;\r\n    width: 125px;\r\n    height: 138px;\r\n    position: absolute;\r\n    top: 53px;\r\n    transform: translate(-250px, 0);\r\n    transition: transform 0.9s ease-in-out;\r\n    background-color: rgb(255, 255, 255);\r\n    border-radius: 5px;\r\n    display: block;\r\n  }\r\n  #drawer ul li {\r\n    display: block;\r\n    padding: 6px 0;\r\n  }\r\n  #drawer li a {\r\n    color: black;\r\n    font-size: 17px;\r\n    padding: 11px 2px;\r\n  }\r\n  #drawer.open {\r\n    transform: translate(0, 0);\r\n  }\r\n  #hamburger {\r\n    display: inline;\r\n  }\r\n  div#jumbotron {\r\n    height: 250px;\r\n  }\r\n  *.wrapper {\r\n    grid-template-columns: repeat(1, 0.99fr);\r\n    justify-content: center;\r\n  }\r\n  div.card.menu-content {\r\n    border: 1px solid rgb(255, 255, 255);\r\n    border-radius: 5px;\r\n    position: relative;\r\n  }\r\n  div.card img {\r\n    height: 170px;\r\n  }\r\n  div.card h5 {\r\n    font-size: 16px;\r\n  }\r\n  div.card p {\r\n    font-size: 13px;\r\n  }\r\n\r\n  div.card .ribbon {\r\n    width: 4rem;\r\n    padding: 4px 3px;\r\n    font-size: 14px;\r\n    text-align: center;\r\n  }\r\n\r\n  div.card .rating {\r\n    font-size: 12px;\r\n  }\r\n  #review-input {\r\n    width: 400px;\r\n  }\r\n  .customer-review,\r\n  #review-input {\r\n    width: 96%;\r\n  }\r\n}\r\n"],"sourceRoot":""}]);
